        p Silva. (2020), afirma que, en un mundo donde la transformacin digital se ha acelerado, y con un mercado sellado por una competencia extrema, se hace vital que toda marca cuente con una estrategia comercial de ventas que se centre en la satisfaccin de los clientes.
        p Si la marca desea suministrar la mejor experiencia a los clientes, requiere tener una estrategia comercial bien diseada, la cual enlace con el consumidor y lo motive no solo a comprar, sino a formar parte de la tribu de marca, es decir, conjunto de personas que estn identificados con un producto, compartiendo ciertos argumentos y teniendo nociones parecidas sobre la marca.
        .cajon.color-primario.p-4
          p Hoy en da, en pleno siglo XXI, contar con una estrategia comercial es cuestin de supervivencia. Aproximadamente el 80 % de las compaas que no disean nuevas estrategias de #[em marketing] y ventas desaparecen del mercado. 

    .row.justify-content-center.align-items-end.mb-5
      .col-lg-8.col-md-12.col-sm-12
        p La estrategia comercial se puede definir como el grupo de acciones que una empresa puede poner en prctica con el fin de dar a conocer un producto nuevo, para aumentar su participacin de mercado. Esta  se plasma en una hoja de ruta, un plan diseado de manera personalizada segn el contexto y necesidades de cada empresa, de acuerdo con sus caractersticas y filosofa empresarial (objetivos, principios, misin, visin y valores).
        p En una estrategia comercial de ventas se unen un conjunto de acciones racionales y elegidas dentro de la compaa . Se deben centrar en el cliente y sus acciones tienen como fin poder posicionar la marca en el mercado. Va mucho ms all del aumento en las ventas ya que su direccin apunta a la perduracin de la marca y sus ventas en el tiempo, garantizando de esta manera su existencia.
