          .row.col-xl-11.m-auto(titulo="No existe estrategia  sin contexto")
            .row.justify-content-center.align-items-center
              p Para que la estrategia marche, debe dar respuesta a la pregunta En dnde se encuentra la empresa?. La respuesta debe incluir datos reveladores sobre su situacin actual en el ao que termina, as como tener en cuenta variables globales e internas que pueden impactar en el funcionamiento de la empresa. Segn sea el contexto empresarial se disea la estrategia, siempre con mirar a un mejoramiento continuo, con base en ese anlisis de su situacin actual.
          .row.col-xl-11.m-auto(titulo="Necesidad de una visin y una misin")
            .row.justify-content-center.align-items-center
              p La visin es el norte de la marca, si se visualiza, entonces se puede alcanzar. No se pueden planificar estrategias si no se sabe a dnde se quiere llegar. Por su lado, la misin, es el ahora, quin se es en la actualidad. Por otro lado, una estrategia comercial para que sea exitosa debe ser realista, lo que implica tener claro cules son las metas a las que la empresa puede dar cumplimiento segn sus recursos, en un perodo determinado de tiempo. Por eso, la visin y la misin son fundamentales.
          .row.col-xl-11.m-auto(titulo="La estrategia comercial debe ser medible y flexible")
            .row.justify-content-center.align-items-center
              p La ejecucin de la estrategia comercial se debe medir en cada una de sus fases, por lo que los indicadores deben estar preestablecidos. El resultado debe permitir un ajuste en la estrategia en pos de lograr un mejoramiento continuo.

