    p.mb-4 Lo primero que se debe hacer es diferenciar qu es estrategia comercial de ventas y qu es estrategia de #[em marketing], ya que no tienen el mismo fin. La estrategia de #[em marketing] se orienta en la generacin de oportunidades en el mercado, mientras que la estrategia comercial de ventas se aprovecha de estas oportunidades para lograr la rentabilidad empresarial.

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-8.col-md-12.col-sm-12
        .cajon.color-acento-botones.p-4.mb-4
          p La estrategia comercial le da prioridad al cierre del proceso de ventas con los clientes ya conocidos. Adems de incluir #[b acciones que se dirigen a los clientes de la competencia y, en menor grado, a captar clientes que an no conocen la marca.] Hay varios criterios que sirven para lograr la clasificacin de los tipos de estrategia comercial. Son criterios que se operan de manera cotidiana en el mbito empresarial. 
    
    p.mb-4 Hay un criterio tradicional que divide la estrategia comercial en dos tipos esenciales:

    .row(data-aos="fade-down")
      .col-lg-6.col-md-12.col-12
        .tarjeta-avatar
          img.mt-2(src='@/assets/curso/temas/tema2/tar2-1.png' alt='')
          .row.justify-content-center.align-items-center.mt-5.mb-5.mx-1
            .tarjeta--boton.tema1
              .p-3
                br
                br
                h4.mt-5.mb-4.text-center Estrategia comercial de productos.
      .col-lg-6.col-md-12.col-12
        .tarjeta-avatar
          img.mt-2(src='@/assets/curso/temas/tema2/tar2-2.png' alt='')
          .row.justify-content-center.align-items-center.mt-5.mb-5.mx-1
            .tarjeta--boton.tema1
              .p-3
                br
                br
                h4.mt-5.mb-4.text-center Estrategia comercial de servicios.

    p A pesar de ser bastante usada en el pasado, esta clasificacin sigue siendo vlida en la actualidad, simplemente porque no es lo mismo vender un producto que un servicio, y las estrategias comerciales deben estar conectadas con este contexto.
    p.mb-4 Con la aparicin de internet, surge un nuevo criterio cuyo uso se ha difundido fuertemente en el mundo empresarial, y que ha dividido los tipos de estrategia comercial en:

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-6.col-md-12.col-sm-12.p-1
        .tarjeta-avatar-b
          .tarjeta-avatar-b__img
            img(src='@/assets/curso/temas/tema2/tar3-1.png' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-3
              .row.align-items-center.justify-content-center.mt-2
                .col-1
                  h1.mt-4.textoazul A.
                .col-10
                  p.mt-4 Estrategias comerciales #[em online].
      .col-lg-6.col-md-12.col-sm-12.p-1
        .tarjeta-avatar-b
          .tarjeta-avatar-b__img
            img(src='@/assets/curso/temas/tema2/tar3-2.png' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-3
              .row.align-items-center.justify-content-center.mt-2
                .col-1
                  h1.mt-4.textoazul B.
                .col-10
                  p.mt-3 Estrategias comerciales #[em offline].

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-8.col-md-12.col-sm-12
        p La estrategia comercial #[em online] incluye todos aquellos tipos de estrategias que la marca desarrolla en el ecosistema digital (redes sociales, correo electrnico, #[em blog], sitio #[em web], etc.), son vitales para las empresas contemporneas. 
        p Por su lado, la estrategia comercial #[em offline] completa todas las acciones tradicionales de ventas desarrolladas a lo largo de la historia. Utiliza canales distintos a internet, pero eso no quiere decir que sean incompatibles.
        p Son muchas las marcas que de manera exitosa unen ambos tipos de estrategia (#[em online] y #[em offline]), dando como resultado una decisin inteligente porque de esta manera se logra el diseo de una estrategia omnicanal que en la actualidad es de amplia aceptacin.  A continuacin, se expondrn los siguientes pasos para el desarrollo de una estrategia comercial efectiva.      
      .col-lg-4.col-md-6.col-6
        img(src='@/assets/curso/temas/tema2/img7.png', alt='')

    .row
      .col-sm-12.col-md-12.col-lg-12

        .tarjeta.tarjeta--rosada.p-4
          LineaTiempoC.color-acento-contenido(text-small)
            .row.justify-content-center.align-items-center(titulo="Paso 1")
              .col-md-3
                img(src='@/assets/curso/temas/tema2/ps1-1.png', alt='Texto que describa la imagen')
              .col-md-8.mb-4.mb-md-0
                h4.mb-4  Elaborar la matriz DOFA
                p Para la ejecucin de este paso se debe tener el brief empresarial, especialmente conocer el contexto y la filosofa de la empresa, o sea, la visin, misin y valores declarados por la misma.
            .row.justify-content-center.align-items-center(titulo="Paso 2")
              .col-md-3
                img(src='@/assets/curso/temas/tema2/ps1-2.png', alt='Texto que describa la imagen')
              .col-md-8.mb-4.mb-md-0
                h4.mb-4  Definir los objetivos comerciales
                p Es importante saber que estos objetivos no comprenden solo nmeros de ventas, sino tambin la penetracin del mercado, la fidelizacin de los clientes, el posicionamiento de marca, la percepcin e imagen de la marca, entre otros aspectos.
            .row.justify-content-center.align-items-center(titulo="Paso 3")
              .col-md-3
                img(src='@/assets/curso/temas/tema2/ps1-3.png', alt='Texto que describa la imagen')
              .col-md-8.mb-4.mb-md-0
                h4.mb-4  Establecer la cartera de clientes
                p Tener muy claro cul es el segmento al cual van dirigidos los productos de la marca. Resulta imposible desarrollar acciones de ventas si no se conoce quines son y cmo piensan los clientes a los cuales se dirigir cada producto.
            .row.justify-content-center.align-items-center(titulo="Paso 4")
              .col-md-3
                img(src='@/assets/curso/temas/tema2/ps1-4.png', alt='Texto que describa la imagen')
              .col-md-8.mb-4.mb-md-0
                h4.mb-4  Definir responsables, plazos y mecanismos de control para cada una de las acciones que se diseen
                p Es importante contar con un registro de las personas que hacen parte del equipo de ventas y los ejecutivos comerciales, de tal forma que puedan acceder a un ciclo de trabajo uniforme y concreto, donde se perciba quin es el responsable de cada accin, qu fechas de cumplimiento tiene y cules son los mecanismos de control que se tienen, todo con el fin de poder tener oportunidades de mejora en cuanto se tengan indicadores. 
                p La mayora de las empresas que trabajan con sistemas de control de ventas tienen mejores resultados e ingresos ms altos.
            .row.justify-content-center.align-items-center(titulo="Paso 5")
              .col-md-3
                img(src='@/assets/curso/temas/tema2/ps1-5.png', alt='Texto que describa la imagen')
              .col-md-8.mb-4.mb-md-0
                h4.mb-4  Utilizar la informacin del embudo de ventas para disear las acciones de la estrategia comercial
                p Las acciones deben disearse de manera personalizada y para tener informacin sobre los hbitos y el comportamiento del cliente, el embudo es primordial para obtener estos datos. (#[b Embudo de ventas]: describe el proceso de conversin de prospectos a clientes, incluyendo mtricas clave como tasas de conversin y tiempo en cada etapa.)
            .row.justify-content-center.align-items-center(titulo="Paso 6")
              .col-md-3
                img(src='@/assets/curso/temas/tema2/ps1-6.png', alt='Texto que describa la imagen')
              .col-md-8.mb-4.mb-md-0
                h4.mb-4  Disear una estrategia comercial con soporte omnicanal
                p Es el uso creativo de los medios #[em offline] y #[em online] para beneficio de la marca. Las empresas que acogen una estrategia comercial omnicanal tienen mayores posibilidades de retener ms clientes y de lograr mejores resultados.

    Separador
    #t_2_4.titulo-segundo.mb-5
      h2 2.4 Las ventas

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-1.col-md-2.col-2
        img(src='@/assets/curso/temas/tema2/img8.png', alt='')
      .col-lg-11.col-md-12.col-col-sm-12
        p Es vital que los empleados de la empresa, y en especial los ejecutivos de ventas, conozcan los productos de la marca. De esta manera los trabajadores reflejan el compromiso que poseen como colaboradores. No cabe duda de que la razn de ser que tiene toda empresa es satisfacer al cliente, pero para conseguirlo, primero debe conocer las necesidades y lo que motiva al cliente para tomar una decisin de compra; de esta manera se orientan mejor todos los esfuerzos para poder lograr los objetivos trazados (Navarro, 2012).

    .mb-4.d-flex.ps-lg-0
      .d-flex.flex-wrap.align-items-center.pe-3.bg-img-blue
        h3.col.px-4.mb-0.mx-5.text-white(data-aos="zoom-in-left") Factores que afectan a las ventas
        img.icon(src='@/assets/curso/temas/icon.svg') 

    p.mb-4 Una de las ms importantes labores que debe tener un especialista en mercadeo es estar pendiente constantemente del entorno para intentar realizar un pronstico de lo que puede suceder en los prximos meses y aos, porque eso ayuda con la elaboracin de estrategias entre diferentes escenarios o situaciones. Como ejemplo se puede mencionar lo que la sociedad ha vivido con el Covid-19 y la forma en que muchas empresas pudieron sostenerse o incluso, mejorar sus ventas porque disearon estrategias para lograrlo.

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-10.col-md-12.col-sm-12.bordenaranjaredondo.p-5
        .titulo-nuevo.mt-2
          | 
          h4 Entorno externo
        p.mb-4 El entorno externo no depende de la empresa porque tiene variables o factores que nacen fuera de ella, por lo tanto, no pueden ser controlados por la empresa, este se integra por los siguientes factores:
    
    .row.fondocontenedor.justify-content-center.align-items-center.mb-5

      .titulo-sexto.color-acento-contenido
        h5 Figura 2. 
        | Anlisis PESTEL 

      .row.justify-content-center.align-items-center.mb-5
        .col-lg-12
            ImagenInfografica.color-secundario
              template(v-slot:imagen)
                figure
                  img(src='@/assets/curso/temas/tema2/fig2.png', alt='La imagen expone un diagrama circular del anlisis PESTEL, que se utiliza para evaluar el entorno macroeconmico de una empresa. El diagrama est dividido en seis segmentos, cada uno representando una de las categoras del anlisis: Poltico, Econmico, Social, Tecnolgico, Ecolgico y Legal. Cada segmento est coloreado y etiquetado claramente para destacar su categora correspondiente. En el centro del crculo, hay un cono de edificios y una turbina elica, simbolizando el entorno empresarial en su conjunto.', style='')
              .tarjeta.color-acento-contenido.p-4(x="63%" y="16%" numero="+")
                h5 Polticos.
                p.mb-0 Son aquellos factores que se asocian con la clase poltica que pueden influir positiva o negativamente a futuro en la actividad empresarial. Se deben analizar temas como la poltica fiscal, los tratados de libre comercio, posibles cambios polticos en los gobiernos, entre otros, que pueden afectar seriamente en las actividades empresariales.
              .tarjeta.color-primario.p-4(x="74%" y="50%" numero="+")
                h5 Econmicos.
                p.mb-0 Se refiere al poder adquisitivo de la sociedad, las personas slo compran bienes o servicios si poseen dinero. Por esta razn, en este factor se deben tener en cuenta indicadores como el PIB, el tipo de cambio, el ndice de inflacin, los intereses, el cambio del dlar o del euro, entre otras. Con estos indicadores se podr realizar un pronstico de ventas ms ajustado y real.
              .tarjeta.color-secundario.p-4(x="38%" y="85%" numero="+")
                h5 Tecnolgicos.
                p.mb-0 Es un anlisis sobre cmo las nuevas tecnologas pueden cambiar la sociedad en un futuro y cmo pueden afectar a la empresa. La tecnologa puede influir en las estrategias de ventas, y por otro lado puede impulsar la capacidad empresarial para vender con mejores resultados. La inversin I+D de los pases o continentes, nuevas tecnologas relacionadas con el contexto empresarial, el nacimiento de tecnologas disruptivas, cambios en el uso de energa, nuevos mtodos de produccin y distribucin son algunos de los puntos que ameritan ser analizados desde el contexto de la empresa.
              .tarjeta.color-acento-botones.p-4(x="63%" y="85%" numero="+")
                h5 Socioculturales.
                p.mb-0 Se refiere a desarrollar un anlisis sobre qu elementos de la sociedad pueden influir en la empresa y cmo estn cambiando. Se busca el poder identificar aquellas tendencias que tiene la sociedad actual. En esta variable se deben tener presentes el comportamiento demogrfico, los cambios en el nivel de ingresos, los cambios en gustos o modas que puedan repercutir en el nivel de las compras, la diversidad cultural, los valores ticos, los cambios en cmo nos relacionamos o comunicamos, y hasta rasgos religiosos que sean interesantes tenerlos en cuenta. Con este anlisis se pueden identificar los artculos que satisfacen mejor las necesidades del cliente. 
              .tarjeta.p-4(x="26%" y="50%" numero="+" style='border-radius: 8px; background-color: #74D578')
                h5 Ecolgicos.
                p.mb-0 Se refiere a estar al enterado de los posibles cambios normativos desde el contexto ecolgico, y del nivel de conciencia social de este movimiento. La naturaleza siempre nos da enseanzas importantes, ms an en la actualidad porque se ha comenzado a visualizar los resultados negativos por culpa de los abusos en los recursos naturales. Por eso, antes de lanzar un producto al mercado, se debe analizar si la materia prima para la produccin y fabricacin del producto afecta negativamente a la ecologa.
              .tarjeta.p-4(x="38%" y="16%" numero="+" style='border-radius: 8px; background-color: #F3AB1D')
                h5 Legales.
                p.mb-0 Son los cambios en las normativas que estn relacionadas con el contexto empresarial, y que tambin puede influir de forma positiva o negativa. Las licencias, las leyes o normas sobre el empleo, leyes de seguridad y salud en el trabajo, leyes antimonopolios, el entorno legal en el pas de origen de la empresa, derechos de propiedad intelectual, sectores protegidos o regulados, entre otros.

      .row.justify-content-center.align-items-center.mb-5
        .col-lg-10.col-md-12.col-sm-12.bordenaranjaredondo.p-5
          .titulo-nuevo.mt-2
            | 
            h4 Entorno interno
          p.mb-4 El entorno interno s depende de la empresa porque tiene variables o factores que nacen dentro de ella, por lo tanto, se pueden controlar de tal forma que la empresa pueda tener un mejoramiento continuo. Los siguientes son esos factores:

    .titulo-sexto.color-acento-contenido
      h5 Figura 3. 
      | Factores entorno interno 

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-12
          ImagenInfografica.color-secundario
            template(v-slot:imagen)
              figure
                img(src='@/assets/curso/temas/tema2/fig3.png', alt='La imagen detalla  un diagrama circular que representa los factores del entorno interno de una empresa. El crculo est dividido en seis segmentos, cada uno con un cono distinto: una bombilla (Capacidad de investigacin, desarrollo y tecnologa), un trofeo (filosofa empresarial, objetivos), un grfico de barras (desempeo, atencin al cliente), un globo terrqueo (capacidad de produccin y cadena de suministros), un smbolo de dlar (Recursos financieros) y un cono de conversacin (recursos humanos). Se sugiere por la ubicacin la interconexin y la diversidad de estos factores internos. ', style='')
            .tarjeta.color-acento-contenido.p-4(x="65%" y="13%" numero="01")
              h5 Metas, objetivos y cultura
              p.mb-0 Tiene que ver con la filosofa empresarial, o sea sus principios, objetivos, misin, visin y valores de una empresa. Se convierten en su ruta de navegacin, su norte. 
            .tarjeta.color-acento-contenido.p-4(x="74%" y="50%" numero="02")
              h5 Recursos humanos
              p.mb-0 Tiene que ser con los empleados de la empresa y es obligatorio reflexionar sobre la integracin de todos los miembros que hacen parte de la empresa. Se debe analizar en los perfiles, la rotacin y las caractersticas que deben tener los ejecutivos de la fuerza de ventas ya que pueden tener una gran movilidad.
            .tarjeta.color-acento-contenido.p-4(x="66%" y="82%" numero="03")
              h5 Recursos financieros
              p.mb-0 Tiene que ver con el msculo financiero de la empresa. Si tiene buenos recursos financieros podr invertir en la investigacin y desarrollo de nuevos productos o servicios y, adems, capacitar correctamente a quienes laboran en esta rea.
            .tarjeta.color-acento-contenido.p-4(x="35%" y="86%" numero="04")
              h5 Capacidad de produccin y cadena de suministro
              p.mb-0 Tiene que ver con la ubicacin de las plantas productivas, la tecnologa usada por la empresa y el equipo que tiene a su disposicin. Todo esto influye en los planes de mercadeo y ventas. Como el tema es el suministro, la empresa debe prestar atencin especial en garantizar que los productos siempre se encuentren disponibles en el punto de venta. 
            .tarjeta.color-acento-contenido.p-4(x="25%" y="50%" numero="05")
              h5 Capacidad de servicio
              p.mb-0 Tiene que ver con la atencin al cliente. Toda empresa debe cumplir con un alto nivel en la calidad del servicio prestado al cliente. Ofrecer a los clientes un valor agregado y un excelente servicio, antes, durante y despus de una venta, puede hacer la diferencia competitiva.
            .tarjeta.color-acento-contenido.p-4(x="35%" y="13%" numero="06")
              h5 Capacidad de investigacin y desarrollo y tecnologa
              p.mb-0 Tiene que ver con la inversin en tecnologa y en la investigacin de nuevos productos que permita a la empresa estar un paso delante de la competencia. 

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-8.col-md-12.col-sm-12
        .mb-4.d-flex.ps-lg-0
          .d-flex.flex-wrap.align-items-center.pe-3.bg-img-blue
            h3.col.px-4.mb-0.mx-5.text-white(data-aos="zoom-in-left") Tcnicas de ventas
            img.icon(src='@/assets/curso/temas/icon.svg') 
        p.mt-4 Se refiere a los mtodos de venta que tienen como finalidad llegar al cliente potencial a travs de cualquier medio o canal, incluido internet y su ecosistema digital. El proceso de venta tiene unas etapas que comienzan con la ubicacin y calificacin del cliente, y terminan en el cierre de la venta; pero, no todo termina con la venta, ya que un excelente vendedor siempre estar en bsqueda de entregar un servicio y atencin que generen ms valor para el consumidor.
        .cajon.color-primario.p-4.mb-4
          p En el siglo XX, la venta personal era la tcnica ms importante, con vendedores que frecuentemente viajaban de casa en casa para exhibir sus productos. Posteriormente, las ventas  multinivel ganaron relevancia, y los vendedores utilizaban catlogos para mostrar los productos de manera ms publicitaria. En la actualidad, las ventas a distancia, gestionadas por correspondencia, han recobrado mucha fuerza gracias a internet, convirtindose en uno de los medios ms eficaces.
      .col-lg-4.col-md-6.col-6
        img(src='@/assets/curso/temas/tema2/img9.png', alt='')

    .mb-4.d-flex.ps-lg-0
      .d-flex.flex-wrap.align-items-center.pe-3.bg-img-blue
        h3.col.px-4.mb-0.mx-5.text-white(data-aos="zoom-in-left") Venta a distancia
        img.icon(src='@/assets/curso/temas/icon.svg') 
    p.mt-4.mb-4 Mtodo en el que no hay una relacin directa entre el cliente y el asesor de ventas. Se refiere a las ventas por correspondencia, ventas telefnicas, ventas electrnicas y ventas a travs de la televisin. A continuacin, se explicarn cada una de ellas:

    .row.mb-5
      AcordionA.acordion.activo(tipo="b" clase-tarjeta="tarjeta tarjeta--azull")
        .row.justify-content-center.align-items-center(titulo="Venta por correspondencia")
          .col-md-7
            p Se ejecuta a travs de un catlogo que se enva por correo postal o se pone directamente en el buzn del cliente. Puede tener el formato de inserto, que es un anuncio pagado tipo volante o folleto que se ubica dentro de las pginas de las revistas o peridicos. 
            p Este tipo de ventas tiene como ventaja que puede describir la informacin del producto o servicio con gran detalle. Pero, en la actualidad, con la presencia del correo electrnico su uso ha disminuido considerablemente. 
          .col-md-4.mb-4.mb-md-0
            img(src='@/assets/curso/temas/tema2/aco1-1.png', alt='Texto que describa la imagen')
        .row.justify-content-center.align-items-center(titulo="Ventas telefnicas")
          .col-md-7
            p Tambin conocida como #[em telemarketing], especialmente se usa en mercados de gran consumo o para lanzar productos nuevos o en promocin especial.
            P Con la aparicin de la telefona celular y de internet, ya no es tan usual ver esta tcnica en la actualidad. 
          .col-md-4.mb-4.mb-md-0
            img(src='@/assets/curso/temas/tema2/aco1-2.png', alt='Texto que describa la imagen')
        .row.justify-content-center.align-items-center(titulo="Ventas electrnicas")
          .col-md-7
            p Es el tipo de venta que se respalda en la transferencia de datos a travs del ecosistema digital.
            p Es la preferida en la actualidad por la economa que presenta a las empresas en su uso y por la agilidad y versatilidad que tiene a la hora de planear las ventas. Correo electrnico, redes sociales, sitios #[em webs] y hasta el WhatsApp sirven de apoyo.
          .col-md-4.mb-4.mb-md-0
            img(src='@/assets/curso/temas/tema2/aco1-3.png', alt='Texto que describa la imagen')
        .row.justify-content-center.align-items-center(titulo="Ventas por televisin")
          .col-md-7
            p Se conocen tambin como televenta y se refieren a la demostracin de productos o servicios de forma minuciosa; la idea es destacar los beneficios como por ejemplo un precio llamativo (puede ser una oferta o descuento) o tambin las ventajas competitivas.
            p Por lo general, se transmiten diariamente en el mismo horario familiar.
          .col-md-4.mb-4.mb-md-0
            img(src='@/assets/curso/temas/tema2/aco1-4.png', alt='Texto que describa la imagen')
        .row.justify-content-center.align-items-center(titulo="Venta personal")
          .col-md-7
            p En esta tcnica s hay contacto directo entre vendedor y cliente. Puede tener dos variables: ejecutarse por dentro o por fuera de las sedes de la empresa.
            img(src='@/assets/curso/temas/tema2/aco1-5-2.png', alt='')
          .col-md-4.mb-4.mb-md-0
            img(src='@/assets/curso/temas/tema2/aco1-5-1.png', alt='Texto que describa la imagen')

    p.mb-5 A su vez, las ventas internas, se pueden clasificar en:  

    .row.justify-content-center.mb-5
      .col-lg-4.col-md-6.col-12.mb-4.mb-xl-0
        .crd_hover_txt(data-aos="fade-left").borde-redondo
          .crd_hover_txt--img
            figure
              img(src="@/assets/curso/temas/tema2/tar4-1.png", alt="alt")
          .crd_hover_txt--body.cambio
            h4.mb-3.text-center Venta directa
            p.mb-0.text-center.text-white Cuando el cliente ingresa al establecimiento y es atendido de manera personalizada por el vendedor.
      .col-lg-4.col-md-6.col-12.mb-4.mb-xl-0
        .crd_hover_txt(data-aos="fade-left").borde-redondo
          .crd_hover_txt--img
            figure
              img(src="@/assets/curso/temas/tema2/tar4-2.png", alt="alt")
          .crd_hover_txt--body.cambio
            h4.mb-3.text-center Venta en libre servicio
            p.mb-0.text-center.text-white Cuando el cliente realiza una compra en una tienda de autoservicio, de un producto que ya ha elegido y evaluado previamente. Por lo general presenta la ventaja de tener precios ms accesibles al consumidor.
