          .crd_hover_txt--body.cambio
            h4.mb-3.text-center Venta en ferias, salones y exhibiciones
            p.mb-0.text-center.text-white Cuando la empresa expone sus productos o servicios a clientes potenciales en lugares especiales como salones especiales o zonas amplias donde pueda existir un buen ambiente. La empresa puede usar un saln de un hotel, por ejemplo. 

    p.mb-4 Por su lado, en las ventas externas, se pueden clasificar en: 

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-4.col-md-6.col-6
        img(src='@/assets/curso/temas/tema2/img10.png', alt='')
      .col-lg-8.col-md-12.col-sm-12
