          SlyderF(columnas="col-lg-12 col-md-12 col-12")
            .tarjeta.tarjeta--slyder.p-3
              .row.justify-content-center.align-items-center(titulo="xxx")
                .col-md-4.mb-4.mb-md-0
                  img(src='@/assets/curso/temas/tema2/sly3-1.png', alt='Texto que describa la imagen')
                .col-md-8
                  h4.mb-4 Ventas a domicilio.
                  p.mb-4 Cuando la venta se ejecuta en el lugar de ubicacin del cliente y por lo general en ese instante se hace la orden de compra y luego se hace entrega del producto o servicio. Este tipo de ventas pueden ser venta por cita en el domicilio del cliente, cuando el vendedor visita al consumidor con una cita previa acordada por ambos; venta de puerta en puerta, cuando el vendedor, sin pedir cita, toca a la puerta del domicilio o lugar de estada del cliente; y venta en el lugar de trabajo, cuando el vendedor visita al cliente en horario laboral en su oficina, previa cita.
            .tarjeta.tarjeta--slyder.p-3
              .row.justify-content-center.align-items-center(titulo="xxx")
                .col-md-4.mb-4.mb-md-0
                  img(src='@/assets/curso/temas/tema2/sly3-2.png', alt='Texto que describa la imagen')
                .col-md-8
                  h4.mb-4 Venta ambulante.
                  p.mb-4 Cuando la venta no tiene un lugar fijo. Lo que s es seguro es que se realiza en la calle y por lo general no presenta garanta en la calidad del producto.
            .tarjeta.tarjeta--slyder.p-3
              .row.justify-content-center.align-items-center(titulo="xxx")
                .col-md-4.mb-4.mb-md-0
                  img(src='@/assets/curso/temas/tema2/sly3-3.png', alt='Texto que describa la imagen')
                .col-md-8
                  h4.mb-4 Auto venta.
                  p.mb-4 Cuando la empresa establece itinerarios para llevar a los clientes los productos que normalmente son perecederos y se consumen diariamente. El vendedor usa una ruta ya establecida de manera estratgica y entrega los productos a clientes que por lo general son tiendas minoristas.
            .tarjeta.tarjeta--slyder.p-3
              .row.justify-content-center.align-items-center(titulo="xxx")
                .col-md-11
                  h4.mb-4 Venta multinivel.
                  p Cuando se crea una red que se compone por un cliente que se asocia a una empresa para obtener un cdigo que lo autoriza como vendedor de sus productos y puede obtener, con precios ms bajos, los productos directamente de la empresa. Con este cdigo el cliente vendedor obtiene un porcentaje de las ventas que realicen otros clientes que fueron afiliados a travs de ellos. 
                  P De esta manera, el cliente vendedor gana por el descuento del precio del producto, por el porcentaje en las ventas propias, por el porcentaje en las ventas de las personas que afili bajo su auspicio hasta el nivel de profundidad establecido por la empresa de multinivel, adems de obtener bonificaciones y regalas de acuerdo con las normas que la empresa haya establecido. 
                  P De esta forma cada integrante puede construir y dirigir su fuerza de venta afiliando, motivando y adiestrando a otros para que realicen lo mismo que l, hasta el punto de construir su propia organizacin, si as lo desean.

    separador
    #t_2_5.titulo-segundo.mb-5
      h2 2.5 Buenas Prcticas de Manufactura (BPM )

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-1.col-md-2.col-2
        img(src='@/assets/curso/temas/tema2/img11.png', alt='')
      .col-lg-11.col-md-12.col-sm-12
        p Estas  son las normas establecidas oficialmente por las empresas que actualmente regulan a las Plantas Procesadoras de Alimentos (PPA) en relacin con los procedimientos de fabricacin, limpieza y desinfeccin, higiene personal, manipulacin, controles, registros y almacenamiento, garantizando as la calidad y seguridad de los alimentos (Amagua, Coronel, Quintana y Reyes, 2016).

    p.mb-4 El uso de las BPM en la industria de alimentos puede ser empleado alrededor de las PPA o al interior de estas:

    .row.justify-content-center.align-items-center.mb-4(data-aos="zoom-in-left")
      .col-lg.col-md-12.col-sm-12
        .bordeazulredondo.p-4
          .row.align-items-center.justify-content-center
            .col-lg-10.col-md-5.col-5
              img(src='@/assets/curso/temas/tema2/tar5-1.png', alt='').mb-4
          h4.mb-0.text-center En la planta
          p.mb-5.text-center En los pisos, paredes, cielo raso y desages.
          .row.mb-5.mt-5
          .row.mb-3
      .col-lg.col-md-12.col-sm-12
        .bordeazulredondo.p-4
          .row.align-items-center.justify-content-center
            .col-lg-10.col-md-5.col-5
              img(src='@/assets/curso/temas/tema2/tar5-2.png', alt='').mb-4
          h4.mb-0.text-center En los alrededores
          p.mb-5.text-center Zonas externas a las PPA para presentar facilidades sanitarias.
          .row.mb-5
      .col-lg.col-md-12.col-sm-12
        .bordeazulredondo.p-4
