                p.mt-2.mb-0.text-center Tiempo que dura el proceso, tipo de detergente a usar y su concentracin, velocidad de flujo de la solucin detergente usada por la PPA, accin mecnica.

    p.mb-4 Las siguientes son las 4 zonas en las que se  debe poner atencin para tener BPM:

    .row.justify-content-center.align-items-center.mb-4(data-aos="zoom-in-left")
      .col-lg-3.col-md-6.col-12.mb-4
        .bgrosado.p-4.borde-redondo
          h4.mb-3.text-center Zona 1
          .row.align-items-center.justify-content-center
            .col-9
              img(src='@/assets/curso/temas/tema2/tar7-1.png', alt='').mb-4
          p.text-center.mb-4 Superficies que tengan contacto con el producto, bandas transportadoras, mesas de trabajo, sierras, tuberas, tanques, rebanadoras, llenadores, herramientas. 
      .col-lg-3.col-md-6.col-12.mb-4
        .bgrosado.p-4.borde-redondo
