              img(src='@/assets/curso/temas/tema2/tar7-2.png', alt='').mb-4
          p.mb-5.text-center Exterior de los equipos, unidades de refrigeracin, pisos, marcos, coches de transporte.
          .row.mb-5
      .col-lg-3.col-md-6.col-12.mb-4
        .bgrosado.p-4.borde-redondo
