          .row.mb-5

    .mb-4.d-flex.ps-lg-0
      .d-flex.flex-wrap.align-items-center.pe-3.bg-img-blue
        h3.col.px-4.mb-0.mx-5.text-white(data-aos="zoom-in-left") Mtodos de limpieza
        img.icon(src='@/assets/curso/temas/icon.svg') 

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-4.col-md-6.col-6
        img(src='@/assets/curso/temas/tema2/img12.png', alt='')
      .col-lg-8.col-md-12.col-col-sm-12
        p.mb-4 Existen dos mtodos de limpieza:
        .bgrosado.borde-redondo.p-4
          SlyderF(columnas="col-lg-12 col-md-12 col-12")
            .tarjeta.tarjeta--slyder.p-3
              .row.justify-content-center.align-items-center(titulo="xxx")
                .col-md-4.mb-4.mb-md-0
                  img(src='@/assets/curso/temas/tema2/sly4-1.png', alt='Texto que describa la imagen')
                .col-md-8
                  h4.mb-4 Limpieza en hmedo
                  p.mb-4 Utiliza detergentes y agua. Se usan los siguientes procedimientos: Pre enjuague para eliminar la suciedad y ablandarla, de tal manera que luego sea fcilmente contrarrestada por el detergente. Luego viene la limpieza con el detergente en la que se pueden tener a disposicin compuestos para efectuar la limpieza, como lo son lcalis, fosfatos, cidos, humectantes y agentes quelantes; cada compuesto se aplica segn sea la necesidad.
            .tarjeta.tarjeta--slyder.p-3
              .row.justify-content-center.align-items-center(titulo="xxx")
                .col-md-4.mb-4.mb-md-0
                  img(src='@/assets/curso/temas/tema2/sly4-2.png', alt='Texto que describa la imagen')
                .col-md-8
                  h4.mb-4 Limpieza en seco
                  p.mb-4 Elimina la suciedad seca sin usar agua; se puede desarrollar a travs de aspiradora, soplador (aire a presin), escoba, cepillo, raspador, etc.
            .tarjeta.tarjeta--slyder.p-3
              .row.justify-content-center.align-items-center(titulo="xxx")
                .col-md-4.mb-4.mb-md-0
                  img(src='@/assets/curso/temas/tema2/sly4-3.png', alt='Texto que describa la imagen')
                .col-md-8
                  h4.mb-4 Desinfeccin
                  p.mb-4 Se deben definir planes validados de limpieza y desinfeccin por cada rea y equipo, teniendo en cuenta la frecuencia, mtodo, producto y responsable directo de cada rea. Se debe hacer una seleccin de detergentes y desinfectantes, adems de tener claros los datos tcnicos y precauciones, con disolucin adecuada a la hora de desinfectar. Todas las superficies que entren en contacto con los alimentos incluyen herramientas y guantes, que deben limpiarse y desinfectarse de forma frecuente. Las superficies que se usan para fabricar alimentos de baja humedad deben estar secos y en condiciones sanitarias.
            .tarjeta.tarjeta--slyder.p-3
              .row.justify-content-center.align-items-center(titulo="xxx")
                .col-md-4.mb-4.mb-md-0
                  img(src='@/assets/curso/temas/tema2/sly4-3.png', alt='Texto que describa la imagen')
                .col-md-8
                  h4.mb-4 Monitoreo ambiental
                  p.mb-4 Las pruebas para el control  ambiental son una manera de calcular la efectividad de los procesos sanitarios: evaluar las superficies y aguas de enjuague bioluminiscencia. Protenas descompuestas: mtodo de deteccin colorimtrico (verde, gris, prpura). Se deben tener claro cules son los puntos de monitoreo, los tanques de almacenamiento, las conexiones en sistemas, los cortadores.


    .mb-4.d-flex.ps-lg-0
      .d-flex.flex-wrap.align-items-center.pe-3.bg-img-blue
        h3.col.px-4.mb-0.mx-5.text-white(data-aos="zoom-in-left") Gua de control de plagas
        img.icon(src='@/assets/curso/temas/icon.svg') 

    .row.fondocontenedor1.justify-content-center.align-items-center.mb-5

      .row.justify-content-center.align-items-center
        .col-lg-11.col-md-12.col-sm-12
          p.mb-4.mt-4 Las plagas traen consigo un riesgo peligroso porque acarrean una gran cantidad de microorganismos y parsitos nocivos a travs de su piel, fosas nasales, tracto gastrointestinal y deposiciones, por ello se deben conocer las condiciones de la planta que facilitan la invasin de plagas, especialmente en el almacenamiento de materia prima, depsitos de basura y desperdicios.

      .row.justify-content-center.align-items-center
        .col-lg-10.col-md-12.col-sm-12
          .titulo-nuevo.mt-2
            | 
            h2 Definiciones  para elaborar un Programa de Control de Plagas (PCP):

      .row.justify-content-center.align-items-center.mb-4
        .col-lg-3.col-md-5.col-5
          img(src='@/assets/curso/temas/tema2/img13.png', alt='')
        .col-lg-7.col-md-12.col-sm
          ul.lista-ul--color.color-acento-contenido.fa-ul
            li 
              i.fas.fa-bullhorn
              span.mb-0 #[b Plaga:] especie que puede llegar hacer mucho dao, siendo una amenaza para la empresa y la PPA, especialmente cuando se encuentra en gran cantidad.
            li 
              i.fas.fa-bullhorn
              span.mb-0 #[b Cebo:] es un veneno que se prepara en forma de comida y que va dirigida a las plagas para poderlas exterminar.
            li 
              i.fas.fa-bullhorn
              span.mb-0 #[b Cebar:] poner el cebo seleccionado de manera que sea ingerido por la plaga.
            li 
              i.fas.fa-bullhorn
              span.mb-0 #[b Infestacin:] nmero alto de individuos que tiene una plaga y que es nocivo para la PPA.
            li 
              i.fas.fa-bullhorn
              span.mb-0 #[b Plaguicida:] sustancia destinada para la prevencin o el control de plagas.
            li 
              i.fas.fa-bullhorn
              span.mb-0 #[b Rodenticida:] plaguicida que sirve para controlar roedores, ratones y ratas.
            li 
              i.fas.fa-bullhorn
              span.mb-0 #[b Raticida:] rodenticida que controla solamente ratas.
            li 
              i.fas.fa-bullhorn
              span.mb-0 #[b Fumigacin:] mtodo rpido para controlas las plagas como, por ejemplo, cianuro de calcio y fosfuro de hidrgeno.

    .row.justify-content-center.align-items-center
      .col-lg-10.col-md-12.col-sm-12
        p La gerencia del control de calidad de la planta es la que se encarga del control de plagas. La empresa debe asignar a una persona que se encargue de la Planeacin y Control de Produccin (PCP), quien tendr la autoridad para requerir la ayuda del personal y supervisar el programa. Esta persona se debe capacitar en la manipulacin de plaguicidas y  control de plagas y su responsabilidad es mantener limpios y seguros los alrededores de la planta y su interior. Adems, debe conocer si la planta est ubicada en sitios donde los sistemas de limpieza sean deficientes y donde se almacene una gran cantidad de desperdicios.
      .col-lg-2.col-md-3.col-3
        img(src='@/assets/curso/temas/tema2/img14.png', alt='')
    
    .row(data-aos="fade-down")
      .col-lg-6.col-md-12.col-12
        .tarjeta-avatar
          img.mt-5(src='@/assets/curso/temas/tema2/tar8-1.png' alt='')
          .row.justify-content-center.align-items-center.mt-5.mb-5.mx-1
            .tarjeta--boton.tema1
              .p-4
                br
                br
                br
                h4.text-center.mt-5 Medidas preventivas
                ul.lista-ul--color.color-secundario.fa-ul.mt-4
                  li 
                    i.fas.fa-check-circle
                    span.mb-0 Evitar la entrada de plagas al interior de la planta de alimento.
                  li 
                    i.fas.fa-check-circle
                    span.mb-0 Limitar el acceso directo a las zonas donde se encuentran los alimentos. 
                  li 
                    i.fas.fa-check-circle
                    span.mb-0 Eliminar ambientes favorables que permitan la proliferacin y desarrollo de plagas. 
                  li 
                    i.fas.fa-check-circle
                    span.mb-0 Diligencia rigurosa del programa de limpieza y desinfeccin.
      .col-lg-6.col-md-12.col-12
        .tarjeta-avatar
          img.mt-5(src='@/assets/curso/temas/tema2/tar8-2.png' alt='')
          .row.justify-content-center.align-items-center.mt-5.mb-5.mx-1
            .tarjeta--boton.tema1
              .p-4
                br
                br
                br
                h4.text-center.mt-5 Medidas correctivas
                ul.lista-ul--color.color-secundario.fa-ul.mt-4.mb-5
                  li 
                    i.fas.fa-check-circle
                    span.mb-0 #[b Contra roedores:] trampas y rodenticidas anticoagulantes. 
                  li 
                    i.fas.fa-check-circle
                    span.mb-0 #[b Contra cucarachas:] insecticidas piretroides y del grupo fosforado.
                  li 
                    i.fas.fa-check-circle
                    span.mb-0 #[b Contra moscas:] insecticidas similares y trampas elctricas, luz UV. Las aves son portadores de enfermedades y parsitos peligrosos para la salud (Amagua, Coronel, Quintana y Reyes, 2016).

    separador
    #t_2_6.titulo-segundo
      h2 2.6 Las demostraciones

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-4.col-md-6.col-6
        img(src='@/assets/curso/temas/tema2/img15.png', alt='')
      .col-lg-8.col-md-12.col-sm-12
        p La conviccin es un tema vital en el proceso de ventas. La conviccin radica en mostrar pruebas, hechos que comprueben lo que se afirma por parte de la empresa, bien sea publicitariamente o a travs del equipo de la fuerza de ventas. Por eso al cliente se le debe convencer, pero muy importante, entusiasmarlo a la compra, de lo contrario sera intil e improductivo. #[b El cliente  no solo debe estar convencido de las caractersticas del producto, sino, ms importante an, de las ventajas y beneficios que este le proporciona al comprarlo.] Por ello, la demostracin es una accin vital y decisiva a travs de la cual el vendedor puede persuadir a sus clientes.
        p La demostracin debe reunir estas cuatro caractersticas bsicas: 
        .row
          .col-lg-6.col-md-12.col-sm-12
            .row.justify-content-center.align-items-center
              .col-lg-3.col-md-2.col-3
                img(src='@/assets/curso/temas/tema2/icono1.png', alt='')
              .col-lg-9.col-md-10.col-sm-12
                p #[b Comprensin:] entender qu tipo de producto es.
            .row.justify-content-center.align-items-center
              .col-lg-3.col-md-2.col-3
                img.mt-4(src='@/assets/curso/temas/tema2/icono2.png', alt='')
              .col-lg-9.col-md-10.col-sm-12
                p #[b Inters:] generar el deseo de saber ms acerca del producto.
          .col-lg-6.col-md-12.col-sm-12
            .row.justify-content-center.align-items-center
              .col-lg-3.col-md-2.col-3
                img(src='@/assets/curso/temas/tema2/icono3.png', alt='')
              .col-lg-9.col-md-10.col-sm-12
                p #[b Credulidad:] confiar en las ventajas del producto.
            .row.justify-content-center.align-items-center
              .col-lg-3.col-md-2.col-3
                img(src='@/assets/curso/temas/tema2/icono4.png', alt='')
              .col-lg-9.col-md-10.col-sm-12
                p #[b Persuasin:] motivar al posible cliente para que compre el producto por los beneficios exhibidos en la demostracin.

    .row.mb-4
      p Entonces, a la hora de la conviccin, se requiere de un factor que refuerce los argumentos en una venta, y ese factor se llama demostracin. #[b Sin la demostracin se hace complejo poder llegar al cierre de una compra] (Universidad Amrica Latina - UAL, 2021).
      p La demostracin se puede considerar como el mtodo que tiene un vendedor comercial para que el cliente se interese en el producto a travs de su exposicin de manera personalizada, para que no quepan dudas de que realmente es lo que necesita. 
      p Debe desarrollarse de manera #[b dinmica y espectacular para captar mejor la atencin del cliente.] Bsicamente, consta de dos fases:

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-6.col-md-12.col-sm-12.p-1
        .tarjeta-avatar-b
          .tarjeta-avatar-b__img
            img(src='@/assets/curso/temas/tema2/tar9-1.png' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-3
              .row.align-items-center.justify-content-center
                .col-1
                  h1.mt-4.textonaranja 1
                .col-11
                  p.mt-4 #[b Primera fase:] interesar al posible cliente exhibindole de forma inteligente los argumentos.
      .col-lg-6.col-md-12.col-sm-12.p-1
        .tarjeta-avatar-b
          .tarjeta-avatar-b__img
            img(src='@/assets/curso/temas/tema2/tar9-2.png' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-3
              .row.align-items-center.justify-content-center
                .col-1
                  h1.mt-2.textonaranja 2
                .col-11
                  p.mt-2 #[b Segunda fase:] convencer al posible cliente a travs de la exposicin de pruebas, demostrando con hechos las ventajas y beneficios que otorga el producto.

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-8.col-md-12.col-sm-12
        p Las demostraciones deben crear algo ms que lograr el inters por conocer ms acerca del producto, deben generar curiosidad y una accin de compra al instante. Por eso, en la primera fase, los argumentos deben ser claros, concisos y completos, para que el posible cliente tenga toda la informacin a la mano. Despus de esto, la demostracin debe ser una exhibicin tan significativa que resalte la importancia de la oferta. 
        p Adems, es el medio ms eficiente para comprobar  las afirmaciones sobre cualquier producto, teniendo en cuenta lo siguiente:
        ul.lista-ul--color.color-secundario.fa-ul.mt-4.mb-5
          li 
            i.fas.fa-check-circle
            span.mb-0 #[b Convence ms] que las simples palabras, ya que se basa en la prctica.
          li 
            i.fas.fa-check-circle
            span.mb-0 #[b Es ms memorable,] haciendo que el producto sea difcil de olvidar.
          li 
            i.fas.fa-check-circle
            span.mb-0 #[b Clarifica] conceptos que podran ser abstractos.
          li 
            i.fas.fa-check-circle
            span.mb-0 #[b Ahorra tiempo] span al exponer de manera personalizada las ventajas y beneficios del producto.
      .col-lg-4.col-md-6.col-6
        img(src='@/assets/curso/temas/tema2/img16.png', alt='')

    .mb-4.d-flex.ps-lg-0
      .d-flex.flex-wrap.align-items-center.pe-3.bg-img-blue
        h3.col.px-4.mb-0.mx-5.text-white(data-aos="zoom-in-left") La demostracin como medio promocional de ventas
        img.icon(src='@/assets/curso/temas/icon.svg') 

    .row.align-items-center.justify-content-center
      .col-lg-4.col-md-6.col-6
        img(src='@/assets/curso/temas/tema2/img17.png', alt='')
      .col-lg-8.col-md-12.col-col-sm-12
        p Un vendedor comercial, siendo un excelente orador y exhibidor de productos, puede mejorar su actuacin utilizando otros canales como el audio y el video para reforzar su mensaje, una tcnica conocida como demostracin.
        p.mb-4 Cuando se enfrenta a un posible cliente, es crucial considerar las diferencias individuales en relacin con los medios visuales. Si el cliente no comprende bien la presentacin oral, el vendedor debera usar medios visuales de comunicacin. Esto resalta la importancia de las cuatro caractersticas bsicas de la demostracin: comprensin, inters, credulidad y persuasin, para motivar la compra.
        .cajon.color-primario.p-5.mb-5
          .h5 Preguntarse. Qu se puede mostrar al cliente para realzar la presentacin? 
          p Es fundamental contar con, videos, imgenes, fotografas, multimedia y presentaciones digitales, con las cuales se puede hacer la demostracin ms efectiva. Incluso, un testimonio de un cliente satisfecho puede ser muy convincente .
    
    p Al exhibir las ventajas del producto, el vendedor debe aprovechar la acumulacin espacial y la selectividad de la percepcin. Mantener cautivos los ojos y odos del cliente aumenta las posibilidades de venta. La eleccin del canal adecuado puede manipular la atencin del cliente, incrementando el realismo y facilitando la comprensin de la demostracin.
    p Para complementar se exponen los siguientes principios del saber: 


    .row.align-items-center.justify-content-center
      .col-sm-12.col-md-12.col-lg-8
          PasosA.color-acento-contenido.mb-5(tipo="n")
            .row.align-items-end
              .col-auto
                h4.mb-4  Participacin
                p La prctica hace al maestro. La educacin es un proceso activo, por eso, cuanto ms se realice una labor, ms, aprender.
            .row.align-items-end
              .col-auto
                h4.mb-4  Asociacin
                p Si se emplean ms canales sensitivos, ms oportunidades existirn de tener un aprendizaje por asociacin. 
            .row.align-items-end
              .col-auto
                h4.mb-4  Transferencia
                p Si los materiales usados por el vendedor demuestran el empleo del producto ms oportunidades existirn para que el posible cliente sea capaz de comprender y visualizar el uso del producto en su propia empresa o para s mismo. Muy usado cuando se trata de productos y servicios tcnicos.
            .row.align-items-end
              .col-auto
                h4.mb-4  Discernimiento
                p nicamente la presentacin entrega al cliente detalles significativos sobre la oferta; pero se puede requerir de la demostracin para exponer el empleo correcto de los productos y servicios, junto con sus ventajas y beneficios. 

    p.mt-0 Las herramientas que se utilizan en la demostracin comprenden un nmero muy amplio de formas, y pueden variar segn sea la necesidad del vendedor comercial, por ejemplo, desde una lista de precios hasta una demostracin del producto a gran escala. 
    p.mb-4 A continuacin, se presentan las herramientas que se utilizan con ms frecuencia: 

    .row.fondo1.align-items-center.p-3(data-aos="fade-down").mb-5
      .col-lg-12.col-md-12.col-12.px-lg-5.px-4
        .row.justify-content-center.mb-4
        
        .tarjetaf.p-3.mb-4
          SlyderA(tipo="b").slider-a-custom(data-aos="zoom-in-left")
            .row.justify-content-center.align-items-center.p-4
              .col-lg-7.col-md-12.col-sm-12
                h4.mb-4 Fotografas
                p.mb-4 Son mejores las fotos en color que en blanco y negro. El color debe mostrar al producto tal cual es, de forma realista. Se recomienda mostrar el producto en accin y no de manera aislada. Tambin se pueden usar ampliaciones en algunas piezas especficas del producto para exponer detalles importantes. 
              .col-lg-4.col-md-6.col-9
                img(src="@/assets/curso/temas/tema2/sly5-1.png")
            .row.justify-content-center.align-items-center.p-4
              .col-lg-7.col-md-12.col-sm-12
                h4.mb-4 Diagramas esquemticos
                p.mb-4 Son considerablemente tiles para transferir ideas sobre productos que son tcnicos, y en este caso el color ayuda a comprenderlos mucho mejor, empleando uno para cada seccin del diagrama. Pueden ser impresos en hojas transparentes (como el acetato) y montados en secuencia para que al final se vea una unidad completa. Por ejemplo, ir viendo las partes de un automvil desde adentro hacia afuera. El diagrama esquemtico tambin puede ser digital o multimedial.
              .col-lg-4.col-md-6.col-9
                img(src="@/assets/curso/temas/tema2/sly5-2.png")
            .row.justify-content-center.align-items-center.p-4
              .col-lg-7.col-md-12.col-sm-12
                h4.mb-4 Copias
                p.mb-4 Son representaciones del artculo en tres dimensiones de manera digital. Las copias deben ser realistas para que sean efectivas; pero, por lo general son simplificadas en comparacin con el producto, porque se disean para mostrar nicamente las caractersticas ms destacadas del artculo.
              .col-lg-4.col-md-6.col-9
                img(src="@/assets/curso/temas/tema2/sly5-3.png")
            .row.justify-content-center.align-items-center.p-4
              .col-lg-7.col-md-12.col-sm-12
                h4.mb-4 Modelos en miniatura
                p.mb-4 Son diferentes a las copias porque son rplicas exactas del producto hechas a escala y de forma tangible. Como ventaja, poseen un mayor realismo comparado con las copias; como desventaja, el costo puede ser elevado.
              .col-lg-4.col-md-6.col-9
                img(src="@/assets/curso/temas/tema2/sly5-4.png")
            .row.justify-content-center.align-items-center.p-4
              .col-lg-7.col-md-12.col-sm-12
                h4.mb-4 Muestras
                p.mb-4 Para artculos de tamao pequeo la mejor demostracin es el mismo producto; no hay nada que se le compare. La muestra debe estar en perfectas condiciones para que logre el mejor impacto. De todas formas, el vendedor no puede sobrepasar la cantidad de muestras indicadas por la misma empresa.
              .col-lg-4.col-md-6.col-9
                img(src="@/assets/curso/temas/tema2/sly5-5.png")
            .row.justify-content-center.align-items-center.p-4
              .col-lg-7.col-md-12.col-sm-12
                h4.mb-4 Pruebas
                p.mb-4 Es cuando se da la oportunidad de experimentar el producto al posible cliente. No se regala, se prueba para que el cliente lo tenga en mano y lo pueda sentir. Videojuegos, televisores y productos de tecnologa usan esta herramienta.
              .col-lg-4.col-md-6.col-9
                img(src="@/assets/curso/temas/tema2/sly5-6.png")
            .row.justify-content-center.align-items-center.p-4
              .col-lg-7.col-md-12.col-sm-12
                h4.mb-4 Demostracin en gran escala
                p.mb-4 Es importante elegir el lugar perfecto para desarrollar la demostracin. Cuando se trata de muchos posibles clientes, el sistema ms econmico es llevarlos al sitio de trabajo de un cliente real para observar el desempeo del producto, o en un lugar amoblado para la demostracin del producto en masa. Por ejemplo, artculos de maquinaria pesada, productos  de sistemas de audio tipo #[em Home Theater].
              .col-lg-4.col-md-6.col-9
                img(src="@/assets/curso/temas/tema2/sly5-7.png")

    .mb-4.d-flex.ps-lg-0
      .d-flex.flex-wrap.align-items-center.pe-3.bg-img-blue
        h3.col.px-4.mb-0.mx-5.text-white(data-aos="zoom-in-left") Requisitos para una buena demostracin
        img.icon(src='@/assets/curso/temas/icon.svg') 

    .row.justify-content-center.align-items-center
      .col-lg-6.col-md-12.col-sm-12
        p La idea es que el vendedor ayude a la audiencia a la comprensin del material que usa en la demostracin, para aprovechar las ventajas que tiene la demostracin, con el fin de lograr exhibir los beneficios que trae el producto para los posibles clientes. El vendedor debe tener un conocimiento prctico de los cinco requisitos primordiales para lograr que la demostracin sea efectiva:
        .row.align-items-center.justify-content-center
          .col-lg-4.col-md-6.col-6
            img(src='@/assets/curso/temas/tema2/tar10-1.png', alt='')
          .col-lg-8.col-md-12.col-col-sm-12
            ul.lista-ul--color.color-acento-contenido.fa-ul.mt-2
              li.mb-0 
                i.fas.fa-check-circle
                span.mb-0 Comprensin clara por parte de la audiencia.
              li.mb-0
                i.fas.fa-check-circle
                span.mb-0 Definicin precisa del objetivo de la demostracin.
              li.mb-0 
                i.fas.fa-check-circle
                span.mb-0 Organizacin excelente del material, canales o herramientas a usar en la demostracin.
              li.mb-0 
                i.fas.fa-check-circle
                span.mb-0 Apoyo de ayudas visuales adecuadas que impacten al posible cliente.
              li.mb-0 
                i.fas.fa-check-circle
                span.mb-0 Demostracin efectiva en el tiempo preciso. 
      .col-lg-6.col-md-12.col-sm-12
        p.mt-3 La manera en que el vendedor ordena su material y sincroniza la demostracin, incide directamente en la seleccin de las herramientas visuales (tales como fotografas, esquemas, grficos, diagramas, pruebas, etc.). Esta seleccin brinda el ms alto potencial para optimizar las demostraciones debido a las siguientes razones:
        .row.align-items-center.justify-content-center
          .col-lg-4.col-md-6.col-6
            img(src='@/assets/curso/temas/tema2/tar10-2.png', alt='')
          .col-lg-8.col-md-12.col-col-sm-12
            ul.lista-ul--color.color-acento-contenido.fa-ul.mt-2
              li.mb-0 
                i.fas.fa-check-circle
                span.mb-0 Hay muy poca informacin sobre el tema, por eso la creatividad debe ser la mejor. 
              li.mb-0
                i.fas.fa-check-circle
                span.mb-0 Las nuevas tendencias en diseo de grfico y sus tcnicas suministran opciones muy importantes que fortalecen la presentacin verbal del vendedor. 
              li.mb-0 
                i.fas.fa-check-circle
                span.mb-0 Las ayudas visuales centran la atencin del cliente potencial en las ventajas y beneficios del producto (Universidad Amrica Latina - UAL, 2021).
           
    separador
    #t_2_7.titulo-segundo.mb-5
      h2 2.7 Las degustaciones
    
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-4.col-md-6.col-6
        img(src='@/assets/curso/temas/tema2/img18.png', alt='')
      .col-lg-8.col-md-12.col-sm-12    
        p Es una actividad promocional que se constituye en una importante herramienta para mostrar al pblico las bondades de un nuevo producto, un nuevo sabor, una nueva frmula o un producto que tuvo cambios y lo mejoraron, entre otros. Hay diferentes opciones para llegar e impactar al cliente potencial con la intencin de que pruebe el producto, que en realidad es el objetivo central de la actividad (Universidad Amrica Latina - UAL, 2021). 
        p Obviamente, posterior a la accin de degustar el producto se encuentra la intencin de motivar la compra de este, aprovechando el apoyo (visual, publicitario, recursos humanos, materiales de trabajo, abastecimiento, logstica, etc.) con lo cual, se cuenta durante toda la actividad . La degustacin se debe situar en los lugares donde exista ms concentracin de clientes, con el objetivo de aumentar el nmero de impactos logrados por degustacin al da. Se sugiere un gran nmero de sitios donde se exhiba la degustacin, es decir, tener una mayor cobertura geogrfica (rea o ciudad que se pretenda impactar).   
        .cajon.color-primario.p-4.mb-4
          p Hay quienes mencionan que esta actividad solo es una buena alternativa para comer en el punto de venta y que no representa un buen beneficio para la marca. Pero, para poder ser ms prudente con el anlisis, se recomienda tener claro cmo estn las ventas del producto en el momento antes de la degustacin, y cmo reaccionan durante y despus de la misma. La medicin final va a permitir el establecimiento bastante preciso sobre la eficacia de la actividad en el punto de venta. 

    p.mb-4 Las siguientes son tres consideraciones que es vital tenerla en cuenta al implementar un plan de degustacin:

    .row.mb-4
      AcordionA.acordion.activo(tipo="b" clase-tarjeta="tarjeta tarjeta--azull")
        .row.justify-content-center.align-items-center(titulo="Preparacin")
          .col-lg-2.col-md-3.col-6
            img(src='@/assets/curso/temas/tema2/aco2-1.png', alt='Texto que describa la imagen')
          .col-lg-8.col-md-12.col-sm-12
            p Hay productos que necesitan ser preparados porque pueden ser cocinados, asados o se les puede agregar algn condimento, dejando esto en responsabilidad de quien exhibe la degustacin. Logrando as, la aceptacin o rechazo, por parte del cliente.  Es importante tener en cuenta que no se puede alterar el sabor original con demasiados condimentos, para no afectar la intencin de la degustacin.
        .row.justify-content-center.align-items-center(titulo="Ejecucin")
          .col-lg-2.col-md-3.col-6
            img(src='@/assets/curso/temas/tema2/aco2-2.png', alt='Texto que describa la imagen')
          .col-lg-8.col-md-12.col-sm-12
            p Es vital que en la creacin del plan de degustacin se tengan en cuenta las negociaciones necesarias con el establecimiento comercial para ejecutar la actividad de forma adecuada. Por ejemplo, la instalacin de un stand o mueble degustador, su tamao, si se requiere de tomacorriente, que est cerca del producto en gndola, cuntas personas estarn presentes haciendo la presentacin de la degustacin, si se necesita refrigeracin, si es necesario un espacio en el establecimiento para guardar materiales o productos, etc. 
        .row.justify-content-center.align-items-center(titulo="Personal")
          .col-lg-2.col-md-3.col-6
            img(src='@/assets/curso/temas/tema2/aco2-3.png', alt='Texto que describa la imagen')
          .col-lg-8.col-md-12.col-sm-12
